.presentation {
    padding: 50px;

    &-info {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 50px;

        &_sub {
            width: 500px;
            padding: 30px;
            font-size: 1.5rem;
        }

        &_link {
            color: #bcd0f7;
        }
    }

    &-profil {
        width: 300px;
        height: 400px;
        margin: 20px 0 0 70px;
        float: left;
        border-radius: 50% 50% 30% 70%/ 60% 30% 70% 40%;
        background-image: url(../../img/2.jpg);
        animation: morph 8s ease-in-out infinite;
        border: 2px black solid;
        background-position: 40%;
        background-size: 360px;
    }

    &-tags {
        display: flex;
        flex-direction: row;
        padding-left: 300px;
        gap: 30px;
        align-items: center;
        text-overflow: ellipsis;
        flex-wrap: wrap;

        & h3 {
            font-size: 1.5rem;
        }

        &-logo {
            width: 40px;
        }
    }
}

@keyframes morph {
    0% {
        border-radius: 50% 50% 30% 70%/ 60% 30% 70% 40%;
    }

    35% {
        border-radius: 70% 30% 50% 50%/ 60% 30% 70% 40%;
    }

    70% {
        border-radius: 30% 70% 80% 20%;
    }

    100% {
        border-radius: 50% 50% 30% 70%/ 60% 30% 70% 40%;

    }
}

@media (max-width: 992px) {
.presentation{
        &-tags {
            padding-left: 0px;
        }
        &-profil{
            width:300px;
        }
    }
        .presentation-info{
            flex-direction: column-reverse;
            &_sub{
                width:350px;
            }
        }
}

@media (max-width: 500px) {
    .presentation {
        &-profil {
            margin: 0px;
            width: 250px;
        }
        &-info {
            flex-direction: column-reverse;
            &_sub {
                width: 200px;
            }
        }
        
    }
}